import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        id:1,
    },
    mutations: {
        setID(state, payload) {
            state.id += payload.payload
        }
    },
    actions: {
        test:function({commit},payload) {
            commit('setID', payload)
        }
    }
})
