<template>
    <transition name="el-zoom-in-center">
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.11 510E 车票出/入库
                </el-col>
            </el-row>
           <!-- 添加button -->
            <el-row class="mrbt">
                <el-col :span="2">
                    <el-button size="mini" type='warning' icon="el-icon-plus" @click="handleAddTicket">添加</el-button>
                </el-col>
            </el-row>
            <!-- 车票记录 -->
            <el-dialog title="车票/票箱明细录入" :visible.sync="dialogFormVisible">
                <el-form 
                    :model="dialogFormData" 
                    :rules="dialogFormDataRules"
                    ref='dialogFormData'
                    status-icon 
                    label-width="120px">
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="票卡物理类型" prop='cardPhysicsType'>
                                <el-input v-model="dialogFormData.cardPhysicsType" prop=''></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="票卡发行商ID" prop='cardIssue'>
                                <el-input v-model.number="dialogFormData.cardIssue" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="车票产品种类" prop='cardType'>
                                <el-input v-model.number="dialogFormData.cardType" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="预赋值属性" prop='prePaymentAttribute'>
                                <el-input v-model.number="dialogFormData.prePaymentAttribute" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="衍生品ID" prop='deriveProductId'>
                                <el-input v-model.number="dialogFormData.deriveProductId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="预赋值金额" prop='prePaymentMoney'>
                                <el-input v-model="dialogFormData.prePaymentMoney" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="车票状态" prop='cardStatus'>
                                <el-input v-model="dialogFormData.cardStatus" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="车票数量" prop='cardNumber'>
                                <el-input v-model="dialogFormData.cardNumber" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="运输方式" prop='transportationMode'>
                                <el-input v-model="dialogFormData.transportationMode" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="票箱编号" prop='boxId'>
                                <el-input v-model="dialogFormData.boxId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('dialogFormData')">
                        重置数据
                    </el-button>
                    <el-button type="primary" @click="dialogFormSubmit('dialogFormData')">
                        添加
                    </el-button>
                </div>
            </el-dialog>
            <!-- 车票记录 -->
            <el-row class='table-history mrbt' v-show="tableVisble">
                <el-table :data="dialogForm2table" style="width: 100%" size='mini' max-height="250">
                    <el-table-column fixed prop="cardPhysicsType" label="票卡物理类型" width="100">
                    </el-table-column>
                    <el-table-column prop="cardIssue" label="票卡发行商ID" width="100">
                    </el-table-column>
                    <el-table-column prop="cardType" label="车票产品种类" width="100">
                    </el-table-column>
                    <el-table-column prop="prePaymentAttribute" label="预赋值属性" width="80">
                    </el-table-column>
                    <el-table-column prop="deriveProductId" label="衍生产品ID" width="100">
                    </el-table-column>
                    <el-table-column prop="prePaymentMoney" label="预赋值金额" width="80">
                    </el-table-column>
                    <el-table-column prop="cardStatus" label="车票状态" width="80">
                    </el-table-column>
                    <el-table-column prop="cardNumber" label="车票数量" width="120">
                    </el-table-column>
                    <el-table-column prop="transportationMode" label="运输方式" width="120">
                    </el-table-column>
                    <el-table-column prop="boxId" label="票箱编号" width="120">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="80">
                        <template slot-scope="scope">
                            <el-button @click.native.prevent="deleteRow(scope.$index, dialogForm2table)" type="danger" size="small">
                                移除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row>
                <el-col :span="16" :offset="2">
                    <el-form ref="mainForm" :model="mainForm" label-width="160px" :rules="mainFormRules">
                        <el-form-item label="车票/票箱数量" prop='cardAndBoxDetailNumber'>
                            <el-input v-model="mainForm.cardAndBoxDetailNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="配送批号" prop="deliveryBatchNumber">
                            <el-input v-model="mainForm.deliveryBatchNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="调度命令日期" prop="dataOfDispatch">
                            <el-input v-model="mainForm.dataOfDispatch"></el-input>
                        </el-form-item>
                        <el-form-item label="调配方式" prop="DispatchWay">
                            <el-radio-group v-model="mainForm.DispatchWay">
                                <el-radio :label="1" size="mini" border>出库</el-radio>
                                <el-radio :label="2" size="mini" border>入库</el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="调入/调出方位置类型" prop="locationType">
                            <el-radio-group v-model="mainForm.locationType">
                                <el-radio :label="1" size="mini" border>线路中心级管理</el-radio>
                                <el-radio :label="2" size="mini" border>票务中心级管理</el-radio>
                                <el-radio :label="3" size="mini" border>车站级管理</el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="调入/调出方位置编码" prop="locationCode">
                            <el-input v-model="mainForm.locationCode"></el-input>
                        </el-form-item>
                        <el-form-item label="运送操作员ID" prop="DeliveryOpId">
                            <el-input v-model="mainForm.DeliveryOpId"></el-input>
                        </el-form-item>
                        <el-form-item :label="operateType+'数量'" prop="inOutWarehouseCardNumber">
                            <el-input v-model.number="mainForm.inOutWarehouseCardNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="调度数量" prop="dispatchNumber">
                            <el-input v-model="mainForm.dispatchNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="空票箱数量" prop="emptyBoxes">
                            <el-input v-model="mainForm.emptyBoxes"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="mainFormSubmit('mainForm')">
                                <i class="iconfont antd-icon-edit-square"></i>
                                执行{{operateType}}
                            </el-button>
                            <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
    </transition>
</template>
<script>
export default {
    name:'',
        data() {
            return {
                dialogFormVisible: false,
                dialogFormData:{
                    cardPhysicsType:'',
                    cardIssue:'',
                    cardType:'',
                    prePaymentAttribute:'',
                    deriveProductId:'',
                    prePaymentMoney:'',
                    cardStatus:'',
                    cardNumber:'',
                    transportationMode:'',
                    boxId:''
                },
                dialogFormDataRules:{
                    cardPhysicsType: [
                        { required: true, message: '请选择操作类型', trigger: 'change' }
                    ],
                    cardIssue: [
                        { required: true, message: '请输入票箱ID', trigger: 'change' },
                        { type: 'number', message: '票箱ID必须为数字值' }
                    ],
                    cardType: [
                        { required: true, message: '请输入设备id', trigger: 'change' },
                        { type: 'number', message: '设备id必须为数字值' }
                    ],
                    prePaymentAttribute: [
                        { type: 'number', required: true, message: '请输入设备物理类型', trigger: 'change' }
                    ],
                    deriveProductId: [
                        { type: 'number', required: true, message: '请输入票卡发行商ID', trigger: 'change' }
                    ],
                    prePaymentMoney: [
                        { required: true, message: '请输入车票产品种类', trigger: 'change' }
                    ],
                    cardStatus: [
                        { required: true, message: '请输入预赋值属性', trigger: 'change' }
                    ],
                    cardNumber: [
                        { required: true, message: '请输入预赋值属性', trigger: 'change' }
                    ],
                    transportationMode: [
                        { required: true, message: '请输入预赋值属性', trigger: 'change' }
                    ],
                    boxId: [
                        { required: true, message: '请输入预赋值属性', trigger: 'change' }
                    ]
                },
                dialogForm2table:[

                ],
                mainForm: {
                    cardAndBoxDetailNumber: '',
                    deliveryBatchNumber: '',
                    dataOfDispatch: '',
                    DispatchWay: '',
                    locationType: '',
                    locationCode: '',
                    DeliveryOpId: '',
                    inOutWarehouseCardNumber: '',
                    dispatchNumber:'',
                    emptyBoxes:'',
                    cardAndBoxDetails:[],
                },
                mainFormRules: {
                    cardAndBoxDetailNumber: [
                        { required: true, message: '请输入活动名称', trigger: 'change' },
                        { min: 1, max: 5, message: '长度在 3 到 5 个字符', trigger: 'change' }
                    ],
                    deliveryBatchNumber: [
                        { required: true, message: '请选择活动区域', trigger: 'change' }
                    ],
                    dataOfDispatch: [
                        { required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    DispatchWay: [
                        { required: true, message: '请选择时间', trigger: 'change' }
                    ],
                    locationType: [
                        { required: true, message: '请至少选择一个活动性质', trigger: 'change' }
                    ],
                    locationCode: [
                        { required: true, message: '请选择活动资源', trigger: 'change' }
                    ],
                    DeliveryOpId: [
                        { required: true, message: '请填写活动形式', trigger: 'change' }
                    ],
                    inOutWarehouseCardNumber: [
                        { required: true, message: '请填写活动形式', trigger: 'change' }
                    ],
                    dispatchNumber: [
                        { required: true, message: '请填写活动形式', trigger: 'change' }
                    ],
                    emptyBoxes: [
                        { required: true, message: '请填写活动形式', trigger: 'change' }
                    ],
                    cardAndBoxDetails: [
                        { required: true, message: '请填写活动形式', trigger: 'change' }
                    ]
                }
            }
       },
    computed: {
        tableVisble() {
            return this.dialogForm2table.length === 0 ? false : true
        },
        operateType() {
            const length = this.mainForm.DispatchWay.length
            if (length === 0) {
                return ''
            }
            else {
                if (this.mainForm.DispatchWay === 1) {
                    return '出库'
                }
                else if (this.mainForm.DispatchWay === 2) {
                    return '入库'
                }
                else {

                }
            }
        }
    },
    methods: {
        handleAddTicket() {
            this.dialogFormVisible = true
        },
        dialogFormSubmit(formName) {
            let addTableData = this.dialogFormData
            let target = Object.assign({}, addTableData)
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    vm.dialogForm2table.push(target)
                    vm.resetForm('dialogFormData')
                    vm.dialogFormVisible = false
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            
            this.$refs[formName].resetFields();
            
        },
        mainFormSubmit(formName) {
            const length = this.dialogForm2table.length
            if (length === 0) {
                this.$message({
                    type: 'error',
                    message: '请录入车票详情',
                    duration: 2000
                })
                return
            }
            this.mainForm.cardAndBoxDetails = this.dialogForm2table
            const target = this.mainForm
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                     vm.$notify({
                            type: 'success',
                            message: '操作成功',
                            duration: 2000
                        })
                        vm.resetForm('mainForm')
                        vm.dialogForm2table = []
                    // vm.$axios('/success').then(res => {
                       
                    // }).catch(err => {
                    //     vm.$notify({
                    //         type: 'error',
                    //         message: '操作失败',
                    //         duration: 2000
                    //     })
                    // })
                    // vm.dialogForm2table.push(target)
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        deleteRow(index, rows) {
            rows.splice(index, 1);
        },
     
        handleCreateForm() {

        }
    }
}
</script>
<style lang="css" scoped>
</style>