<template>
    <transition name='el-zoom-in-center'>
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.13 5100 车票领用/归还
                </el-col>
            </el-row>
              <!-- 添加button -->
            <el-row class="mrbt">
                <el-col :span="2">
                    <el-button size="mini" type='warning' icon="el-icon-plus" @click="handleAddTicket">添加</el-button>
                </el-col>
                <el-col :span="4">
                    <el-radio-group v-model="addType">
                        <el-radio label="1" border size="mini">票箱</el-radio>
                        <el-radio label="2" border size="mini">散票</el-radio>
                    </el-radio-group>
                </el-col>
            </el-row>
            <!-- 明细录入 dialog -->
            <el-dialog title="明细录入" :visible.sync="dialogVisible">
                <el-form 
                    :model="dialogForm" 
                    :rules="dialogFormRules"
                    ref='cardBoxForm'
                    status-icon 
                    label-width="120px">
                    <el-row>
                        <el-col :span="11" v-show="boxIdVisible">
                            <el-form-item label="票箱ID" prop='cardPhysicsType'>
                                <el-input v-model="dialogForm.boxId" prop=''></el-input>
                            </el-form-item>
                        </el-col>
                         <el-col :span="11">
                            <el-form-item label="票卡物理类型" prop='cardPhysicsType'>
                                <el-input v-model="dialogForm.cardPhysicsType" prop=''></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="票卡发行商ID" prop='cardIssue'>
                                <el-input v-model.number="dialogForm.cardIssue" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                         <el-col :span="11">
                            <el-form-item label="车票产品种类" prop='cardType'>
                                <el-input v-model.number="dialogForm.cardType" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="预赋值属性" prop='prePaymentAttribute'>
                                <el-input v-model.number="dialogForm.prePaymentAttribute" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="衍生品ID" prop='deriveProductId'>
                                <el-input v-model.number="dialogForm.deriveProductId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="车票数量" prop='cardNumber'>
                                <el-input v-model="dialogForm.cardNumber" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                         <el-col :span="11">
                            <el-form-item label="预赋值金额" prop='prePaymentMoney'>
                                <el-input v-model="dialogForm.prePaymentMoney" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('dialogFormData')">
                        重置数据
                    </el-button>
                    <el-button type="primary" @click="dialogFormSubmit('dialogFormData')">
                        添加
                    </el-button>
                </div>
            </el-dialog>
            <!-- 车票记录 -->
            <el-row class='table-history mrbt' v-show="tableVisibleCardbox">
                票箱明细
                <el-table :data="dialogForm2tableCardbox" style="width: 100%" size='mini' max-height="250">
                    <el-table-column prop="boxId" label="票箱编号">
                    </el-table-column>
                    <el-table-column fixed prop="cardPhysicsType" label="票卡物理类型" >
                    </el-table-column>
                    <el-table-column prop="cardIssue" label="票卡发行商ID" >
                    </el-table-column>
                    <el-table-column prop="cardType" label="车票产品种类">
                    </el-table-column>
                    <el-table-column prop="prePaymentAttribute" label="预赋值属性">
                    </el-table-column>
                    <el-table-column prop="deriveProductId" label="衍生产品ID">
                    </el-table-column>
                    <el-table-column prop="prePaymentMoney" label="预赋值金额">
                    </el-table-column>
                    <el-table-column prop="cardNumber" label="车票数量">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="80">
                        <template slot-scope="scope">
                            <el-button @click.native.prevent="deleteRow(scope.$index, dialogForm2tableCardbox)" type="danger" size="small">
                                移除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row class='table-history mrbt' v-show="table2VisibleCard">
                散票明细
                <el-table :data="dialogForm2tableCard" style="width: 100%" size='mini' max-height="250">
                    <el-table-column fixed prop="cardPhysicsType" label="票卡物理类型">
                    </el-table-column>
                    <el-table-column prop="cardIssue" label="票卡发行商ID" >
                    </el-table-column>
                    <el-table-column prop="cardType" label="车票产品种类">
                    </el-table-column>
                    <el-table-column prop="prePaymentAttribute" label="预赋值属性">
                    </el-table-column>
                    <el-table-column prop="deriveProductId" label="衍生产品ID">
                    </el-table-column>
                    <el-table-column prop="prePaymentMoney" label="预赋值金额">
                    </el-table-column>
                    <el-table-column prop="cardNumber" label="车票数量">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="80">
                        <template slot-scope="scope">
                            <el-button @click.native.prevent="deleteRow(scope.$index, dialogForm2tableCard)" type="danger" size="small">
                                移除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row v-show="mainFormVisible">
                <el-col :span='16' :offset="2">
                    <el-form ref="mainForm" :model="mainForm" label-width="160px">
                        <el-form-item label="票箱领用/归还数">
                            <el-input v-model="mainForm.boxReceiveOrReturnNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="散票领用/归还数">
                            <el-input v-model="mainForm.boxReceiveOrReturnNumber"></el-input>
                        </el-form-item>
                         <el-form-item label="现金金额">
                            <el-input v-model="mainForm.cashAmount"></el-input>
                        </el-form-item>
                        <el-form-item label="操作员ID">
                            <el-input v-model="mainForm.operatorId"></el-input>
                        </el-form-item>
                        <el-form-item label="操作类型">
                            <el-radio-group v-model="mainForm.operateType">
                                <el-radio label="1" border size="mini">领用</el-radio>
                                <el-radio label="2" border size="mini">归还</el-radio>
                            </el-radio-group>
                        </el-form-item>
                       
                        <el-form-item>
                            <el-button type="primary" @click="mainFormSubmit('mainForm')">
                                <i class="iconfont antd-icon-left-square"></i>
                                执行{{operateType}}</el-button>
                            <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
    </transition>
</template>
<script>
    export default {
        name:'',
        data(){
            return {
                addType:'0',
                dialogVisible:false,
                dialogForm: {
                    boxId: '01020304',
                    cardPhysicsType: 1,
                    cardIssue: 1,
                    cardType: 1,
                    prePaymentAttribute: 1,
                    deriveProductId: 1,
                    cardNumber: 100,
                    prePaymentMoney: 100
                },
                dialogFormRules:{

                },
                dialogForm2tableCardbox:[],
                dialogForm2tableCard:[],
                mainForm:{
                    'operatorId': '123456',
                    'operateType': '',
                    'cashAmount': 100,
                    'boxReceiveOrReturnNumber': 2,
                    'boxReceiveOrReturns':[],
                    'outBoxReceiveOrReturnNumber':2,
                    'outBoxReceiveOrReturns':[]
                },
            }
        },
        computed:{
            tableVisibleCardbox() {
                return this.dialogForm2tableCardbox.length === 0 ? false : true
            },
            table2VisibleCard() {
                return this.dialogForm2tableCard.length === 0 ? false : true
            },
            mainFormVisible() {
                return this.dialogForm2tableCard.length === 0 && this.dialogForm2tableCardbox.length === 0 ?false : true
            },
            boxIdVisible() {
                 return this.addType*1 === 1 ? true : false
            },
            operateType() {
                const length = this.mainForm.operateType.length
                if (length === 0) {
                    return ''
                }
                else {
                    if (this.mainForm.operateType*1 === 1) {
                        return '领用'
                        
                    }
                    else if (this.mainForm.operateType*1 === 2) {
                        return '归还'
                        
                    }
                    else {
                        
                    }
                }
            }
        },
        methods:{
            handleAddTicket() {
                if(this.addType*1 === 1) {
                    this.dialogVisible = true
                } 
                else if(this.addType*1 === 2) {
                    this.dialogVisible =true
                }
                else {
                    this.$message({
                        type:'error',
                        message:'请选择票箱/散票',
                        duration:2000
                    })
                }
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            dialogFormSubmit(formName) {
                let addTableData = this.dialogForm
                let target = Object.assign({}, addTableData)
                const vm = this
                if (this.addType*1 ===1 ) {
                    this.dialogForm2tableCardbox.push(target)
                }
                else if (this.addType*1 ===2) {
                    delete target.boxId 
                   this.dialogForm2tableCard.push(target)
                }
                // this.$refs[formName].validate(function (valid) {
                //     if (valid) {
                        
                       
                //     } else {
                //         console.log('error submit!!');
                //         return false;
                //     }
                // });
            },
            deleteRow(index, rows) {
                rows.splice(index, 1);
            },
            mainFormSubmit(formName) {
                this.mainForm.boxReceiveOrReturns = this.dialogForm2tableCardbox
                this.mainForm.outBoxReceiveOrReturns = this.dialogForm2tableCard
                const target = this.mainForm
                const vm = this
                this.$refs[formName].validate(function (valid) {
                    if (valid) {
                        vm.$notify({
                            type: 'success',
                            message: '操作成功',
                            duration: 2000
                        })
                        vm.resetForm('mainForm')
                        vm.dialogForm2tableCardbox = []
                        vm.dialogForm2tableCard = []
                        // vm.$axios('/success').then(res => {
                        
                        // }).catch(err => {
                        //     vm.$notify({
                        //         type: 'error',
                        //         message: '操作失败',
                        //         duration: 2000
                        //     })
                        // })
                        // vm.dialogForm2table.push(target)
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>
<style lang="">
    
</style>