<template>
    <div>
        <el-row style="margin-bottom:10px;">
            <el-col :span='6'>
                <el-button 
                    type="primary" 
                    size="mini"
                    @click="storeOperate('1')">
                    <i class="el-icon-circle-plus-outline"></i>
                    入库</el-button>
                <el-button 
                    type="danger" 
                    size="mini"
                    @click="storeOperate('2')">
                    <i class="el-icon-remove-outline"></i>
                    出库</el-button>
                 <el-button type="warning" size="mini">
                    <i class="el-icon-edit"/>库存调整</el-button>
            </el-col>
        </el-row>
        <el-table
            ref="singleTable"
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange">
            <el-table-column
                type="selection"
                width="55">
            </el-table-column>
            <el-table-column label="编号" width="50" prop="id">
            </el-table-column>
            <el-table-column
                label="库存位置">
                <template slot-scope="scope">{{ scope.row.date }}</template>
            </el-table-column>
            <el-table-column
                prop="name"
                label="库存类型">
            </el-table-column>
            <el-table-column
                prop="address"
                label="库存数量"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                prop="address"
                label="库存状态"
            show-overflow-tooltip>
            </el-table-column>
        </el-table>
        <el-dialog
            title="入库"
            :visible.sync="dialogVisible"
            width="70%"
            :before-close="handleClose">
            <!-- 认证操作员模块 -->
            <el-row>
                <el-col :span="3">
                    认证操作员
                </el-col>

                <el-col :span="3">
                    认证操作员密码
                </el-col>
                <el-col :span="8">
                    入库原因等等内容预留部分
                </el-col>
            </el-row>
            <!-- 入库填写表格 -->
            <el-table :data="inStoreTableData" 
                class="tb-edit" style="width: 100%" 
                highlight-current-row >
                <el-table-column label="库存位置" width="180">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.date" placeholder="" ></el-input>
                    </template>
                </el-table-column>
                <el-table-column label="库存类型" width="180">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.name" placeholder=""></el-input>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" label="库存数量">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.address" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template scope="scope">
                        <!--<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>-->
                        <el-button size="small" type="danger" @click="inStoreTableDeleteRow(scope.$index, scope.row)">
                            <i class="el-icon-delete"></i>
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-row>
                <el-col :span="2">
                     <el-button type="warning" size="mini" @click="inStoreTableAddRow">
                        <i class="el-icon-plus"></i>
                        新增
                    </el-button>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="inStoreSure">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 第二种入库 填写方式  出库填写方式 ，operateType 控制页面显示及选项-->
        <el-dialog
            :title="operateType"
            :visible.sync="storeDiaogVisible"
            width="70%"
            :before-close="handleClose">
            <!-- 认证操作员模块 -->
            <el-row>
                <el-col :span="3">
                    认证操作员
                </el-col>

                <el-col :span="3">
                    认证操作员密码
                </el-col>
                <el-col :span="8">
                    入库原因等等内容预留部分
                </el-col>
            </el-row>
            <!-- 入库填写表格 -->
            <el-table :data="secondInStoreTableData" 
                class="tb-edit" style="width: 100%" 
                highlight-current-row >
                 <el-table-column label="编号" width="50" prop="id">
                </el-table-column>
                <el-table-column label="库存位置" width="180">
                    <template scope="scope">
                        <span>{{scope.row.date}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="库存类型" width="180">
                    <template scope="scope">
                        <span>{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" label="库存数量">
                    <template scope="scope">
                        <span>{{scope.row.address}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" :label="operateType + '数量'">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.number" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
                 <el-table-column prop="scope" label="备注">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.remark" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="storeDiaogVisible= false">取 消</el-button>
                <el-button type="primary" @click="storeSureChange">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 出库操作 -->
        <!-- <el-dialog 
            title="出库操作"
            :visible.sync="storeDiaogVisible"
            width="70%"
            :before-close="handleClose">
         <el-table :data="secondInStoreTableData" 
                class="tb-edit" style="width: 100%" 
                highlight-current-row >
                 <el-table-column label="编号" width="50" prop="id">
                </el-table-column>
                <el-table-column label="库存位置" width="180">
                    <template scope="scope">
                        <span>{{scope.row.date}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="库存类型" width="180">
                    <template scope="scope">
                        <span>{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" label="库存数量">
                    <template scope="scope">
                        <span>{{scope.row.address}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="scope" label="出库数量">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.number" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
                 <el-table-column prop="scope" label="备注">
                    <template scope="scope">
                        <el-input size="small" v-model="scope.row.remark" placeholder="" @change="handleEdit(scope.$index, scope.row)"></el-input>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="storeDiaogVisible = false">取 消</el-button>
                <el-button type="primary" @click="outStoreSureChange">确 定</el-button>
            </span>
        </el-dialog> -->
        <!-- <el-table :data="changeableTableData" class="tb-edit" style="width: 100%" highlight-current-row @row-click="handleCurrentChange">
            <el-table-column label="库存位置" width="180">
                <template scope="scope">
                    <el-input size="small" v-model="scope.row.date" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input> <span>{{scope.row.date}}</span>
                </template>
            </el-table-column>
            <el-table-column label="库存类型" width="180">
                <template scope="scope">
                    <el-input size="small" v-model="scope.row.name" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input> <span>{{scope.row.name}}</span>
                </template>
            </el-table-column>
            <el-table-column prop="库存数量" label="地址">
                <template scope="scope">
                    <el-input size="small" v-model="scope.row.address" placeholder="请输入内容" @change="handleEdit(scope.$index, scope.row)"></el-input> <span>{{scope.row.address}}</span>
                </template>
            </el-table-column>
            <el-table-column
                prop="address"
                label="库存状态"
            show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="操作">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table> -->
    </div>
</template>

<script>
  export default {
    data() {
        return {
            tableData: [{
                id:'1',
                date: '新郑机场',
                name: '预付值-4元',
                address: '10000'
                },{
                id:'2',
                date: '新郑机场',
                name: '预付值单程漂-4元',
                address: '10000'
                },
                {
                id:'3',
                date: '新郑机场',
                name: '预付值-2元',
                address: '10000'
                },
                {
                id:'4',
                date: '新郑机场',
                name: '预付值单程漂-8元',
                address: '10000'
                },
                {
                id:'5',
                date: '新郑机场',
                name: '预付值单程漂-12元',
                address: '10000'
                },
                {
                id:'6',
                date: '新郑机场',
                name: '预付值单程漂-1元',
                address: '10000'
                },
                {
                id:'7',
                date: '新郑机场',
                name: '预付值单程漂-3元',
                address: '10000'
            }],
            multipleSelection: [],
            dialogVisible:false,
            inStoreTableData:[{
                date: '',
                name: '',
                address: ''
            }],
            secondInStoreTableData:[

            ],
            storeDiaogVisible:false,
            operateType:''
        }
    },

    methods: {
        // 1入库 操作
        storeOperate(index) {
            if (index*1 === 1) {
                this.operateType= '入库'
                if (this.multipleSelection.length === 0) {
                    this.dialogVisible = true
                    return
                }
            }
            else {
                this.operateType = '出库'
                if (this.multipleSelection.length === 0) {
                    return
                }
            }
            this.storeDiaogVisible = true
            const target = this.multipleSelection.map(item => {
                return Object.assign({},item,{
                    number:'',
                    remark:''
                })
            })
            this.secondInStoreTableData = target
        },
        handleClose(done) {
            done();
            // this.$confirm('确认关闭？')
            //     .then(_ => {
            //         done();
            //     })
            //     .catch(_ => {});
        },
        // 1-入库删除按钮
        inStoreTableDeleteRow(index, row) {
            if(this.inStoreTableData.length === 1) {
                return
            }
            this.inStoreTableData.splice(index,1)
        },
        inStoreTableAddRow() {
             this.inStoreTableData.push({
                date: '',
                name: '',
                address: ''
             })
        },
        inStoreSure() {
            this.dialogVisible = false
            this.$notify({
                message:"操作成功",
                type:'success'
            })
            const target = this.inStoreTableData.map(item =>{
                return  Object.assign({},item,{
                    a:'正常'
                })
            })
            this.tableData.push(...target)
        },
        // inStoreSureChange
        storeSureChange() {
            this.storeDiaogVisible = false
            this.secondInStoreTableData.map(item => {
                const id = item.id
                let value
                if(this.operateType ==='出库') {
                    value = item.address*1 - item.number*1
                }
                else {
                    value = item.address*1 + item.number*1
                }
                this.tableData.map(row => {
                    if(row.id === id) {
                        row.address = value
                    }
                })
            })
             this.$notify({
                message:'操作成功',
                type:'success'
            })
        },
        // toggleSelection(rows) {
        //     if (rows) {
        //         rows.forEach(row => {
        //             this.$refs.multipleTable.toggleRowSelection(row);
        //         });
        //     } else {
        //         this.$refs.multipleTable.clearSelection();
        //     }
        // },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            this.changeableTableData = val
        },
        handleCurrentChange(row, event, column) {
            // console.log(row, event, column, event.currentTarget)
        },
        handleEdit(index, row) {
            // console.log(index, row);
            this.inStoreTableData.slice(index,1)
        },
        handleDelete(index, row) {
            this.changeableTableData.splice(index,1)
        },
        handeleAdd() {
            this.changeableTableData.push( {
                date: '',
                name: '',
                address: ''
            })
        }
    }
  }
</script>