<template>
   <transition name="el-zoom-in-center">
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.10 510D 票箱压票/清点
                </el-col>
            </el-row>
            <el-row>
                    <el-col :span='16' :offset="2">
                        <el-form 
                            ref="form" 
                            :model="form"
                            status-icon 
                            :rules="rules"
                            label-width="160px">
                            <el-form-item label="操作类型"  prop='operateType'>
                                <el-radio-group v-model="form.operateType">
                                    <el-radio :label="1" border size="mini">压票</el-radio>
                                    <el-radio :label="2" border size="mini">清点</el-radio>
                                </el-radio-group>
                            </el-form-item>
                            <el-form-item label="票箱ID" prop='boxId'>
                                <el-input v-model="form.boxId"></el-input>
                            </el-form-item>
                            <el-form-item label="设备ID" prop='devId'>
                                <el-input v-model="form.devId"></el-input>
                            </el-form-item>
                            <el-form-item label="设备物理类型" prop='cardPhysicsType'>
                                <el-input v-model="form.cardPhysicsType"></el-input>
                            </el-form-item>
                            <el-form-item label="票卡发行商ID" prop='cardIssue'>
                                <el-input v-model="form.cardIssue"></el-input>
                            </el-form-item>
                            <el-form-item label="车票产品种类" prop='cardType'>
                                <el-input v-model="form.cardType"></el-input>
                            </el-form-item>
                            <el-form-item label="预赋值属性" prop='prePaymentAttribute'>
                                <el-input v-model="form.prePaymentAttribute"></el-input>
                            </el-form-item>
                            <el-form-item label="衍生产品ID" prop='deriveProductId'>
                                <el-input v-model="form.deriveProductId"></el-input>
                            </el-form-item>
                            <el-form-item :label="computedNum1" prop='box1Num'>
                                <el-input v-model="form.box1Num"></el-input>
                            </el-form-item>
                            <el-form-item :label="computedNum2" prop='box2Num'>
                                <el-input v-model="form.box2Num"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitForm('form')">
                                    <i class="iconfont antd-icon-appstore"></i>
                                    执行{{operateType}}
                                </el-button>
                                <el-button type='warning' @click="resetForm('form')">重置表单</el-button>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
            </div>
   </transition>
</template>
<script>
    export default {
        name:'',
        data() {
            return {
                form: {
                    operateType: '',
                    boxId: '',
                    devId: '',
                    cardPhysicsType: '',
                    cardIssue: '',
                    cardType: '',
                    prePaymentAttribute: '',
                    deriveProductId: '',
                    box1Num: '',
                    box2Num:''
                },
                rules:{
                    operateType: [
                        { required: true, message: '请选择操作类型', trigger: 'change' }
                    ],
                    boxId: [
                        { required: true, message: '请输入票箱ID', trigger: 'change' },
                        // { type: 'number', message: '票箱ID必须为数字值' }
                    ],
                    devId: [
                        { required: true, message: '请输入设备id', trigger: 'change' },
                        // { type: 'number', message: '设备id必须为数字值' }
                    ],
                    cardPhysicsType: [
                        { required: true, message: '请输入设备物理类型', trigger: 'change' }
                    ],
                    cardIssue: [
                        {  required: true, message: '请输入票卡发行商ID', trigger: 'change' }
                    ],
                    cardType: [
                        { required: true, message: '请输入车票产品种类', trigger: 'change' }
                    ],
                    prePaymentAttribute: [
                        { required: true, message: '请输入预赋值属性', trigger: 'change' }
                    ],
                    deriveProductId: [
                        { required: true, message: '请输入衍生品ID', trigger: 'change' }
                    ],
                    box1Num: [
                        { required: true, message: '请输入票卡数量', trigger: 'change' }
                    ],
                    box2Num: [
                        { required: true, message: '请输入票卡数量', trigger: 'change' }
                    ]
                }
            }
        },
        computed: {
            // operateType:function() {
            //     return this.form.operateType?this.form.operateType : '执行'
            // },
            operateType: function () {
                const length = this.form.operateType.length
                if (length === 0) {
                    return ''
                }
                else {
                    if (this.form.operateType === 1) {
                        return '压票'
                    }
                    else if (this.form.operateType === 2) {
                        return '清点'
                    }
                    else {

                    }
                }
            },
            computedNum1:function() {
                if(this.operateType == '压票'){
                    return '压票前车票数量'
                }
                else if (this.operateType == '清点') {
                    return '已记录车票数量'
                }
                else {
                    return '票卡数量1'
                }
            },
            computedNum2:function() {
                if (this.operateType == '压票') {
                    return '压票后车票数量'
                }
                else if (this.operateType == '清点') {
                    return '清点实际车票数量'
                }
                else {
                    return '票卡数量2'
                }
            }
        },
        methods: {
            submitForm:function(formName) {
                let postData = this.form
                const vm = this
                this.$refs[formName].validate(function(valid){
                    if (valid) {
                         vm.$notify({
                            type: 'success',
                            message: '操作成功',
                            duration: 2000
                        })
                        vm.resetForm('form')
                        // vm.dialogForm2tableCardbox = []
                        // vm.dialogForm2tableCard = []
                        // this.$axios('./get/add').then((res)=>{

                        // }).cath((error) => {

                        // })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm: function(formName) {
                this.$refs[formName].resetFields();
            }
        }
    }
</script>
<style lang="">
    
</style>