<template>
   <transition name='el-zoom-in-center'>
        <div>
             <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.15 5113 操作员现金归还
                </el-col>
            </el-row>
            <el-row>
                <el-col :span='16' :offset="2">
                    <el-form ref="mainForm" :model="mainForm" label-width="160px">
                        <el-form-item label="操作员ID" prop="operatorId">
                            <el-input v-model="mainForm.operatorId"></el-input>
                        </el-form-item>
                        <el-form-item label="应归还金额" prop="shouldReturnMoney">
                            <el-input v-model="mainForm.shouldReturnMoney"></el-input>
                        </el-form-item>
                        <el-form-item label="实际归还金额" prop="actualReturnMoney">
                            <el-input v-model="mainForm.actualReturnMoney"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="mainFormSubmit('mainForm')">
                                <i class='iconfont antd-icon-xiayibu'></i>
                                归还
                            </el-button>
                            <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
   </transition>
</template>
<script>
    export default {
        name:'',
        data(){
            return {
                mainForm: {
                    operatorId: '',
                    shouldReturnMoney: '',
                    actualReturnMoney: ''
                }
            }
        },
        methods:{
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            mainFormSubmit(formName) {
                const target = this.mainForm
                const vm = this
                this.$refs[formName].validate(function (valid) {
                    if (valid) {
                        vm.$notify({
                            type: 'success',
                            message: '操作成功',
                            duration: 2000
                        })
                        vm.resetForm('mainForm')
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>
