<template>
    <transition name="el-zoom-in-center">
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                    3.3.12 510F 库存数量调整
                </el-col>
            </el-row>
            <el-row v-show="!formCtrlShow">
                <el-col :span='16' :offset="2">
                    <el-form ref="idForm" :model="idForm" label-width="160px">
                        <el-form-item label="认证操作员ID">
                            <el-input v-model="idForm.id"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="idCheckSubmit">认证</el-button>
                            <el-button>取消</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
            <el-row v-show="formCtrlShow" class="mrbt">
                <el-col :span="2">
                    <el-button type='warning' size="mini" icon="el-icon-plus" @click="newAdd">
                        新增
                    </el-button>
                </el-col>
            </el-row>
            <el-dialog title="车票/票箱明细录入" :visible.sync="dialogFormVisible">
                <el-form 
                    :model="dialogFormData" 
                    :rules="dialogFormDataRules"
                    ref='dialogFormData'
                    status-icon 
                    label-width="120px">
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="票卡物理类型" prop='cardPhysicsType'>
                                <el-input v-model="dialogFormData.cardPhysicsType" prop=''></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="票卡发行商ID" prop='cardIssue'>
                                <el-input v-model.number="dialogFormData.cardIssue" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="车票产品种类" prop='cardType'>
                                <el-input v-model.number="dialogFormData.cardType" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="预赋值属性" prop='prePaymentAttribute'>
                                <el-input v-model.number="dialogFormData.prePaymentAttribute" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="衍生品ID" prop='deriveProductId'>
                                <el-input v-model.number="dialogFormData.deriveProductId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="调整前数量" prop='beforeAdjustNumber'>
                                <el-input v-model="dialogFormData.beforeAdjustNumber" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="调整后数量" prop='afterAdjustNumber'>
                                <el-input v-model="dialogFormData.afterAdjustNumber" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="调整原因" prop='cardNumber'>
                                <el-input v-model="dialogFormData.adjustReason" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="车票状态" prop='cardStatus'>
                                <el-input v-model="dialogFormData.cardStatus" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="预赋值金额" prop='prePaymentMoney'>
                                <el-input v-model="dialogFormData.prePaymentMoney" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button type='danger' @click="resetForm('dialogFormData')">
                        重置数据
                    </el-button>
                    <el-button type="primary" @click="dialogFormSubmit('dialogFormData')">
                        添加
                    </el-button>
                </div>
            </el-dialog>
            <el-row class='table-history mrbt' v-show="tableVisble" >
                <el-table :data="dialogForm2table" style="width: 100%" size='mini' max-height="250">
                    <el-table-column fixed prop="cardPhysicsType" label="票卡物理类型" width="100">
                    </el-table-column>
                    <el-table-column prop="cardIssue" label="票卡发行商ID" width="100">
                    </el-table-column>
                    <el-table-column prop="cardType" label="车票产品种类" width="100">
                    </el-table-column>
                    <el-table-column prop="prePaymentAttribute" label="预赋值属性" width="80">
                    </el-table-column>
                    <el-table-column prop="deriveProductId" label="衍生产品ID" width="100">
                    </el-table-column>
                    <el-table-column prop="beforeAdjustNumber" label="调整前数量" width="80">
                    </el-table-column>
                    <el-table-column prop="afterAdjustNumber" label="调整后数量" width="80">
                    </el-table-column>
                    <el-table-column prop="adjustReason" label="调整原因" width="120">
                    </el-table-column>
                    <el-table-column prop="cardStatus" label="车票状态" width="120">
                    </el-table-column>
                    <el-table-column prop="prePaymentMoney" label="预赋值金额" width="120">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="80">
                        <template slot-scope="scope">
                            <el-button @click.native.prevent="deleteRow(scope.$index, dialogForm2table)" type="danger" size="small">
                                移除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row v-show="tableVisble">
                 <el-col :span='16' :offset="2">
                    <el-form ref="mainForm" :model="mainForm" :rules="mainFormRules" label-width="160px">
                         <el-form-item label="库存调整数量">
                            <el-input v-model="dialogForm2tableLength">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="更新方式" prop="DispatchWay">
                            <el-radio-group v-model="mainForm.DispatchWay">
                                <el-radio :label="1" size="mini" border>手工操作</el-radio>
                                <el-radio :label="2" size="mini" border>盘点</el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="mainFormSubmit('mainForm')">
                                <i class="iconfont antd-icon-edit-square"></i>
                                立刻调整
                            </el-button>
                            <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
    </transition>
</template>
<script>
export default {
    name:'stocknumberoperate',
    data() {
        return {
            idForm:{
                id:''
            },
            formCtrlShow:false,
            dialogFormData:{
                cardPhysicsType: 1,
                cardIssue: 1,
                cardType: 1,
                prePaymentAttribute: 1,
                deriveProductId: 1,
                beforeAdjustNumber: 10,
                afterAdjustNumber: 20,
                adjustReason: '工作需要',
                cardStatus: 1,
                prePaymentMoney: 100
            },
            dialogFormDataRules:{

            },
            dialogFormVisible:false,
            dialogForm2table:[],
            mainForm: {
                updateMode:'',
                authOperatorId:'',
                adjustRepertoryNumber:'',
                adjustRepertorys:[]
            },
             mainFormRules: {
                    DispatchWay: [
                        { required: true, message: '请选择更新方式', trigger: 'change' },
                    ]
                }
        }
    },
    computed:{
        tableVisble() {
            return this.dialogForm2table.length === 0 ? false : true
        },
        dialogForm2tableLength() {
            return this.dialogForm2table.length
        }
    },
    mounted() {
        
    },
    methods:{
        idCheckSubmit() {
            if (this.idForm.id*1 === 123456) {
                 this.$message({
                    type:'success',
                    message:'认证成功',
                    duration:2000
                })
                this.formCtrlShow = true
            }
            else {
                this.$message({
                    type:'error',
                    message:'认证失败',
                    duration:2000
                })
            }
        },
        newAdd() {
            this.dialogFormVisible = true
        },
        dialogFormSubmit(formName){
            let addTableData = this.dialogFormData
            let target = Object.assign({}, addTableData)
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    vm.dialogForm2table.push(target)
                    vm.dialogFormVisible = false
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        deleteRow(index, rows) {
            rows.splice(index, 1);
        },
        mainFormSubmit(formName) {
            const length = this.dialogForm2table.length
            // if (length === 0) {
            //     this.$message({
            //         type: 'error',
            //         message: '请录入车票详情',
            //         duration: 2000
            //     })
            //     return
            // }
            this.mainForm.adjustRepertorys = this.dialogForm2table
            const target = this.mainForm
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                     vm.$notify({
                        type: 'success',
                        message: '操作成功',
                        duration: 2000
                    })
                    vm.resetForm('mainForm')
                    vm.dialogForm2table = []
                    // vm.$axios('/success').then(res => {
                    //     vm.$notify({
                    //         type: 'success',
                    //         message: '操作成功',
                    //         duration: 2000
                    //     })
                    //     vm.resetForm('mainForm')
                    //     vm.dialogForm2table = []
                    // }).catch(err => {
                    //     vm.$notify({
                    //         type: 'error',
                    //         message: '操作失败',
                    //         duration: 2000
                    //     })
                    // })
                    // vm.dialogForm2table.push(target)
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields()
        }
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
            vm.dialogVisible = true
            // 通过 `vm` 访问组件实例
        })
    }
}
</script>
