<template>
    <transition name="el-zoom-in-center">
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.16 5114 钱箱领用/归还
                </el-col>
            </el-row>
           <!-- 添加button -->
            <el-row class="mrbt">
                <el-col :span="2">
                    <el-button size="mini" type='warning' icon="el-icon-plus" @click="handleAddTicket">添加</el-button>
                </el-col>
            </el-row>
            <!-- 车票记录 -->
            <el-dialog title="钱箱明细录入" :visible.sync="dialogFormVisible">
                <el-form 
                    :model="dialogFormData" 
                    :rules="dialogFormDataRules"
                    ref='dialogFormData'
                    status-icon 
                    label-width="120px">
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="钱箱ID" prop='cashboxId'>
                                <el-input v-model="dialogFormData.cashboxId" prop=''></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="设备ID" prop='devId'>
                                <el-input v-model.number="dialogFormData.devId" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <el-form-item label="是否己清点" prop='isChecked'>
                                <el-input v-model.number="dialogFormData.isChecked" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                         <el-col :span="11">
                            <el-form-item label="币种总数" prop='ticketNumber'>
                                <el-input v-model.number="dialogFormData.ticketNumber" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                         <el-col :span="11">
                            <el-form-item label="fiveCorners" prop='fiveCorners'>
                                <el-input v-model.number="dialogFormData.fiveCorners" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="一元" prop='oneYuan'>
                                <el-input v-model.number="dialogFormData.oneYuan" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        
                    </el-row>
                    <el-row>
                         <el-col :span="11">
                            <el-form-item label="五元" prop='fiveYuan'>
                                <el-input v-model.number="dialogFormData.fiveYuan" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11">
                            <el-form-item label="十元" prop='tenYuan'>
                                <el-input v-model.number="dialogFormData.tenYuan" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                        
                    </el-row>
                    <el-row>
                         <el-col :span="11">
                            <el-form-item label="二十元" prop='twentyYuan'>
                                <el-input v-model.number="dialogFormData.twentyYuan" autocomplete="off"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="resetForm('dialogFormData')">
                        重置数据
                    </el-button>
                    <el-button type="primary" @click="dialogFormSubmit('dialogFormData')">
                        添加
                    </el-button>
                </div>
            </el-dialog>
            <!-- 钱箱记录 -->
            <el-row class='table-history mrbt' v-show="tableVisble">
                <el-table :data="dialogForm2table" style="width: 100%" size='mini' max-height="250">
                    <el-table-column fixed prop="cashboxId" label="钱箱ID">
                    </el-table-column>
                    <el-table-column prop="devId" label="设备ID">
                    </el-table-column>
                    <el-table-column prop="isChecked" label="是否己清点">
                    </el-table-column>
                    <el-table-column prop="ticketNumber" label="币种总数">
                    </el-table-column>
                    <el-table-column prop="fiveCorners" label="fiveCorners" >
                    </el-table-column>
                    <el-table-column prop="oneYuan" label="一元" >
                    </el-table-column>
                    <el-table-column prop="fiveYuan" label="五元" >
                    </el-table-column>
                    <el-table-column prop="tenYuan" label="十元" >
                    </el-table-column>
                    <el-table-column prop="twentyYuan" label="二十元">
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" width="80">
                        <template slot-scope="scope">
                            <el-button @click.native.prevent="deleteRow(scope.$index, dialogForm2table)" type="danger" size="small">
                                移除
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-row>
            <el-row v-show="tableVisble">
                <el-col :span="16" :offset="2">
                    <el-form ref="mainForm" :model="mainForm" label-width="160px" :rules="mainFormRules">
                        <el-form-item label="钱箱总数" prop='cashboxNumber'>
                            <el-input v-model="mainForm.cashboxNumber"></el-input>
                        </el-form-item>
                        <el-form-item label="操作类型" prop="operateType">
                             <el-radio-group v-model="mainForm.operateType">
                                <el-radio label="1" size="mini" border>领用</el-radio>
                                <el-radio label="2" size="mini" border>归还</el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="操作员ID" prop="operatorId">
                            <el-input v-model="mainForm.operatorId"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="mainFormSubmit('mainForm')">
                                <i class="iconfont antd-icon-edit-square"></i>
                                执行{{operateType}}
                            </el-button>
                            <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
    </transition>
</template>
<script>
export default {
    name:'',
    data() {
        return {
            dialogFormVisible: false,
            dialogFormData:{
                cashboxId: '01020304',
                devId: '01020304',
                isChecked: 1,
                ticketNumber: 2,
                fiveCorners: 12,
                oneYuan: 2,
                fiveYuan: 2,
                tenYuan: 2,
                twentyYuan: 2
            },
            dialogFormDataRules:{
                cardPhysicsType: [
                    { required: true, message: '请选择操作类型', trigger: 'change' }
                ],
                cardIssue: [
                    { required: true, message: '请输入票箱ID', trigger: 'change' },
                    { type: 'number', message: '票箱ID必须为数字值' }
                ],
                cardType: [
                    { required: true, message: '请输入设备id', trigger: 'change' },
                    { type: 'number', message: '设备id必须为数字值' }
                ],
                prePaymentAttribute: [
                    { type: 'number', required: true, message: '请输入设备物理类型', trigger: 'change' }
                ],
                deriveProductId: [
                    { type: 'number', required: true, message: '请输入票卡发行商ID', trigger: 'change' }
                ],
                prePaymentMoney: [
                    { required: true, message: '请输入车票产品种类', trigger: 'change' }
                ],
                cardStatus: [
                    { required: true, message: '请输入预赋值属性', trigger: 'change' }
                ],
                cardNumber: [
                    { required: true, message: '请输入预赋值属性', trigger: 'change' }
                ],
                transportationMode: [
                    { required: true, message: '请输入预赋值属性', trigger: 'change' }
                ],
                boxId: [
                    { required: true, message: '请输入预赋值属性', trigger: 'change' }
                ]
            },
            dialogForm2table:[],
            mainForm: {
                operateType: '',
                operatorId: '',
                cashboxNumber: '',
                cashboxs:[]
            },
            mainFormRules: {
                operateType: [
                    { required: true, message: '请选择操作类型', trigger: 'change' }
                ],
                operatorId: [
                    { required: true, message: '请输入操作员Id', trigger: 'change' }
                ],
                cashboxNumber: [
                    { required: true, message: '请输入钱箱数量', trigger: 'change' }
                ]
            }
        }
    },
    computed: {
        tableVisble() {
            return this.dialogForm2table.length === 0 ? false : true
        },
        operateType() {
            return this.mainForm.operateType*1 === 1 ? '领用' : '归还'
            // if (this.mainForm.operateType*1 === 1) {
            //     return '领用'
            // }
            // else if (this.mainForm.operateType*1 === 2) {
            //     return '归还'
            // }
            // else {
            // }
        }
    },
    methods: {
        newAddCurrency() {
            this.currencyFormVisible = true
        },
        dialogFormSubmit(formName) {
            let addTableData = this.dialogFormData
            let target = Object.assign({}, addTableData)
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                    vm.dialogForm2table.push(target)
                    vm.resetForm('dialogFormData')
                    vm.dialogFormVisible = false
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            
            this.$refs[formName].resetFields();
            
        },
        mainFormSubmit(formName) {
            const length = this.dialogForm2table.length
            if (length === 0) {
                this.$message({
                    type: 'error',
                    message: '请录入车票详情',
                    duration: 2000
                })
                return
            }
            this.mainForm.cardAndBoxDetails = this.dialogForm2table
            const target = this.mainForm
            const vm = this
            this.$refs[formName].validate(function (valid) {
                if (valid) {
                     vm.$notify({
                            type: 'success',
                            message: '操作成功',
                            duration: 2000
                        })
                        vm.resetForm('mainForm')
                        vm.dialogForm2table = []
                    // vm.$axios('/success').then(res => {
                       
                    // }).catch(err => {
                    //     vm.$notify({
                    //         type: 'error',
                    //         message: '操作失败',
                    //         duration: 2000
                    //     })
                    // })
                    // vm.dialogForm2table.push(target)
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        deleteRow(index, rows) {
            rows.splice(index, 1);
        },
        handleAddTicket() {
            this.dialogFormVisible = true
        },
        handleCreateForm() {

        }
    }
}
</script>
<style lang="css" scoped>
</style>