<template>
    <transition name='el-zoom-in-center'>
        <div>
            <el-row class="my-header">
                <el-col :span='6' :offset='18'>
                   3.3.14 5112 备用金领用归还
                </el-col>
            </el-row>
            <el-row>
                <el-col :span='16' :offset="2">
                    <el-form ref="mainForm" :model="mainForm" label-width="160px">
                        <el-form-item label="操作类型" prop='operateType'>
                            <el-radio-group v-model="mainForm.operateType" >
                                <el-radio label="1" border size="mini">领用</el-radio>
                                <el-radio label="2" border size="mini">归还</el-radio>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="操作员ID" prop='operatorId'>
                            <el-input v-model="mainForm.operatorId"></el-input>
                        </el-form-item>
                        <el-form-item label="上次结存现金金额" prop='lastTimeBalance'>
                            <el-input v-model="mainForm.lastTimeBalance"></el-input>
                        </el-form-item>
                        <el-form-item label="本次操作现金金额" prop='thisTimeCashAmount'>
                            <el-input v-model="mainForm.thisTimeCashAmount"></el-input>
                        </el-form-item>
                        <el-form-item label="本次结存现金金额" prop='thisTimeBalance'>
                            <el-input v-model="mainForm.thisTimeBalance"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="mainFormSubmit('mainForm')">
                                <i class='iconfont antd-icon-transaction'></i>
                                执行{{operateType}}
                            </el-button>
                            <el-button type='danger' @click="resetForm('mainForm')">重置表单</el-button>
                        </el-form-item>
                    </el-form>
                </el-col>
            </el-row>
        </div>
    </transition>
</template>
<script>
    export default {
        name:'',
        data(){
            return {
                mainForm: {
                    operateType: '',
                    operatorId: '',
                    lastTimeBalance: '',
                    thisTimeCashAmount: '',
                    thisTimeBalance: ''
                }
            }
        },
        computed:{
            operateType() {
                const length = this.mainForm.operateType.length
                if (length === 0) {
                    return ''
                }
                else {
                    if (this.mainForm.operateType*1 === 1) {
                        return '领用'
                    }
                    else if (this.mainForm.operateType*1 === 2) {
                        return '归还'
                    }
                    else {

                    }
                }
            }
        },
        methods:{
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            mainFormSubmit(formName) {
                const target = this.mainForm
                const vm = this
                this.$refs[formName].validate(function (valid) {
                    if (valid) {
                        vm.$notify({
                            type: 'success',
                            message: '操作成功',
                            duration: 2000
                        })
                        vm.resetForm('mainForm')
                        // vm.$axios('/success').then(res => {
                        
                        // }).catch(err => {
                        //     vm.$notify({
                        //         type: 'error',
                        //         message: '操作失败',
                        //         duration: 2000
                        //     })
                        // })
                        // vm.dialogForm2table.push(target)
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>
